<template>
    <d2-container>
        <!-- 优惠卷管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="3" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="优惠卷管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="3">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <el-button type="primary" plain @click="add_good">添加</el-button>
                </el-col>
                <el-col :span="3" style="line-height: 40px;">
                    <el-switch v-model="value3" active-text="未失效" active-color="#13ce66" inactive-color="#ff4949" inactive-text="已失效">
                    </el-switch>
                </el-col>
                <el-col :span="3" style="line-height: 40px;">
                    <el-switch v-model="value4" active-text="已发布" active-color="#13ce66" inactive-color="#ff4949" inactive-text="未发布">
                    </el-switch>
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="red_packet_name" label="优惠券名称" width="180">
            </el-table-column>
            <el-table-column prop="red_packet_price" label="优惠券金额" width="180">
            </el-table-column>
            <el-table-column prop="is_red_packet_threshold" label="有无门槛,1=>有门槛,0=>无门槛">
            </el-table-column>
            <el-table-column prop="red_packet_threshold_price" label="门槛金额">
            </el-table-column>
            <el-table-column prop="is_publish" label="是否发布，发布代表所有用户都能领取该红包,1=>发布,0=>暂不发布">
            </el-table-column>
            <el-table-column prop="start_at" label="红包有效开始时间">
            </el-table-column>
            <el-table-column prop="end_at" label="红包有效结束时间">
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <el-button type="primary" plain @click="edit_type(val.row)">编辑</el-button>
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="del_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加优惠卷管理 -->
        <el-dialog :title="num" :visible.sync="centerDialogVisible" width="30%" center :before-close="clear">
            <el-form ref="form" :model="form" label-width="100px">
                <el-form-item label="优惠券名称">
                    <el-input v-model="form.red_packet_name" placeholder="请输入商品类别名称"></el-input>
                </el-form-item>
                <el-form-item label="优惠券金额">
                    <el-input v-model="form.red_packet_price" placeholder="请输入商品类别描述"></el-input>
                </el-form-item>
                <el-form-item label="门槛">
                    <el-input placeholder="有无门槛,1=>有门槛,0=>无门槛" v-model="form.is_red_packet_threshold"></el-input>
                </el-form-item>
                <el-form-item label="门槛金额">
                    <el-input placeholder="门槛金额，如满100才会减免" v-model="form.red_packet_threshold_price"></el-input>
                </el-form-item>
                <el-form-item label="是否发布">
                    <el-input placeholder="是否发布，发布代表所有用户都能领取该红包,1=>发布,0=>暂不发布" v-model="form.is_publish"></el-input>
                </el-form-item>
                <el-form-item label="开始时间">
                    <!-- <el-input placeholder="红包有效开始时间" v-model="form.start_at"></el-input> -->
                    <el-date-picker v-model="form.start_at" type="date" placeholder="红包有效开始时间" style="width: 100%;">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="结束时间">
                    <el-date-picker v-model="form.end_at" type="date" placeholder="红包有效结束时间" style="width: 100%;">
                    </el-date-picker>
                    <!-- <el-input placeholder="红包有效结束时间" v-model="form.end_at"></el-input> -->
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="clear">取 消</el-button>
                <el-button type="primary" @click="onSubmit" v-if="num!=='编辑优惠卷'">确 定</el-button>
                <el-button type="primary" @click="onSubmit_type" v-if="num=='编辑优惠卷'">确 定</el-button>
            </span>
        </el-dialog>

    </d2-container>

</template>

<script>
    import { add_redpacket, show_redpacket, del_redpacket, edit_redpacket } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'coupons_home',
        data() {
            return {
                num: '',
                state1: '',
                checked: 0,
                tableData: [],
                value3: true,
                value4: true,
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    red_packet_name: '',
                    red_packet_price: '',
                    is_red_packet_threshold: '',
                    red_packet_threshold_price: '',
                    is_publish: '',
                    start_at: '',
                    end_at: ''
                },

            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            add_good() {//添加商品
                this.num = "添加优惠卷";
                this.centerDialogVisible = true;
            },
            onSubmit() {//添加商品
                let par = qs.stringify(this.form)
                add_redpacket(par).then(res => {
                    this.msg(res)
                })
            },
            edit_type(a) {//编辑商品
                this.num = "编辑优惠卷";
                this.form = {
                    red_packet_name: a.red_packet_name,
                    red_packet_price: a.red_packet_price,
                    is_red_packet_threshold: a.is_red_packet_threshold,
                    red_packet_threshold_price: a.red_packet_threshold_price,
                    is_publish: a.is_publish,
                    start_at: a.start_at,
                    end_at: a.end_at,
                    id: a.id
                }
                this.centerDialogVisible = true;
            },
            onSubmit_type() {//编辑商品
                let par = {
                    red_packet_name: this.form.red_packet_name,
                    red_packet_price: this.form.red_packet_price,
                    is_red_packet_threshold: this.form.is_red_packet_threshold,
                    red_packet_threshold_price: this.form.red_packet_threshold_price,
                    is_publish: this.form.is_publish,
                    start_at: this.form.start_at,
                    end_at: this.form.end_at
                }
                let id = this.form.id;
                edit_redpacket(par, id).then(res => {
                    this.msg(res)
                })
            },
            del_val(a) {//删除商品
                console.log(a);
                del_redpacket(a.id).then(res => {
                    this.msg(res)
                })
            },
            page(a) {//翻页
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            init_goods() {//初始化
                console.log(this.value4);
                
                let par = {
                    page: this.pages,
                    expire: this.value3 === true ? 1 : 0,
                    is_publish: this.value4 === true ? 1 : 0
                }
                show_redpacket(par).then(res => {
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_goods()

                    return
                }
                Myerror(res, this)
            },
            clear() {//清空
                this.centerDialogVisible = false;
                this.form = {
                    red_packet_name: '',
                    red_packet_price: '',
                    is_red_packet_threshold: '',
                    red_packet_threshold_price: '',
                    is_publish: '',
                    start_at: '',
                    end_at: ''
                }

            }
        },
        mounted() {
            this.init_goods()
        },
        watch: {
            value3(a, b) {
                if (a != b) {
                    this.init_goods()
                }
            },
            value4(a, b) {
                if (a != b) {
                    this.init_goods()
                }
            }
        }
    }
</script>
<style lang="scss">
</style>